*{
    box-sizing: border-box;
}


body{
    margin:0 auto;
    padding: 0;
    max-width:960px;
    font-size:16px;
}
/* Flex */

.inline-flex{
    display:flex;
    align-items:center;
}
.inline-flex li{
    margin-right:0.5rem;
}

/* Form */
.form-group{
    margin:12px 0;
}
form legend{
    font-size:1.2rem;
}
.form-group label{
    margin-right: 0.5rem;
}
.form-group input[type='text']{
    border-radius:3px;
    border:1px solid #989898;
}
.form-group input[type='text']:focus{
    border:1px solid    
}

/* Button */
button{
    cursor:pointer;
    border:none;
    text-align:center;
    text-decoration:none;
    padding:0.5rem;
    border-radius:3px;
    transition-duration:0.4s;
}
.btn-primary{
    background-color:#008CBA;
    color:white;
}
.btn-prime{
    background-color:#00b2ed;
    color:white;
}
.btn-green{
    background-color:#4CAF50;
    color:white;

}
button:hover{
    opacity:0.8;
}
